<template>
  <div id="app" @touchmove.prevent>
    <div @touchmove.stop>
        <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
        <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
        <div class="pause" @touchmove.prevent='draggable' @touchstart='dragstart' ref="dragbar">
          <p @click='slowdown' class="button"><<</p>
          <p @click='pauseAnimate' class="button" ref="pause">pause</p>
          <p @click='speedup' class="button">>></p>
          <p v-html='interval' class="button"></p>
        </div>
    </div>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        pause:false,
        interval: 40,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
* Inspired by http://strml.net/
* Hello everyone! My name is huilingBao.
* I want to write a personal resume.
*/

/* First of all, i would like to add some transition to all of the elements. */
* {
    transition:all .3s;
}
/* White background is too monotonous, let us have some background. */
html {
  color: rgb(222,222,222); background: rgb(0,43,54);
}
/* The text is too close to the border. */
.styleEditor {
  padding: .5em;
  border: 1px solid;
  overflow: auto;
  width: 90vw;
  margin: 2.5vh 5vw;
  height: 90vh;
}
/*  */
.styleEditor {
  height: 45vh;
}
/*  */
.token.selector{
  color: rgb(133,153,0);
}
.token.property{
  color: rgb(187,137,0);
}
.token.punctuation{
  color: yellow;
}
.token.function{
  color: rgb(42,161,152);
}

/*  3D  */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  transform: rotateX(-10deg) translateZ(-50px) ;
}

/*  */
.resumeEditor{
  position: fixed;
  top: 50%; left: 0;
  padding: .5em;  margin: 2.5vh;
  width: 95vw; height: 45vh;
  border: 1px solid;
  background: white; color: #222;
  overflow: auto;
}
/*  */


`,
          `
/* 
 *  Markdown  HR 
 *  HTML 
 */
`
          ,
          `
/*  HTML  */
.resumeEditor{
  padding: 2em;
}
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;
  content: counters(section, ".") " ";
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: #ddd;
}
        `],
        currentMarkdown: '',
        fullMarkdown: `BHL
          ----

          
          ----

          * 
          * Vue 
          * jQuery 

          
          ----

          1.
          2.
          3.
          4.

          
          ----

          * [GitHub](https://github.com/crrusader)
          `,
        offx:'',
        offy:''
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0)
        await this.progressivelyShowResume()
        await this.progressivelyShowStyle(1)
        await this.showHtml()
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true
          this.$nextTick(() => {
            this.$refs.resumeEditor.goTop()
          })
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval
          let showStyle = (async function () {
            let style = this.fullStyle[n]
            if (!style) { return }
            //  n  style 
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length
            if (this.currentStyle.length < length) {
                if( this.pause ){
                    setTimeout(showStyle, this.interval)
                    return
                }
              let l = this.currentStyle.length - prefixLength
              let char = style.substring(l, l + 1) || ' '
              this.currentStyle += char
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom()
                })
              }
              setTimeout(showStyle, this.interval)
            } else {
              resolve()
            }
          }).bind(this)
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length
          let interval = this.interval
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
                if( this.pause ){
                    setTimeout(showResume, this.interval)
                    return
                }
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, this.interval)
            } else {
              resolve()
            }
          }
          showResume()
        })
      },
      pauseAnimate(){
         this.pause = !this.pause
         this.$refs.pause.innerHTML = this.pause?'play':'pause';
      },
      speedup(){
         this.interval <= 0 ? this.interval = 0:this.interval -= 40
     },
      slowdown(){
         if(this.interval >= 400 )return
         this.interval += 40
      },
      dragstart(e){
        let refoffx = this.$refs.dragbar.offsetLeft;
        let refoffy = this.$refs.dragbar.offsetTop;
        let x = e.targetTouches[0].pageX;
        let y = e.targetTouches[0].pageY;
        this.offx = x - refoffx;
        this.offy = y - refoffy;
      },
      draggable(e){
        let x = e.targetTouches[0].pageX;
        let y = e.targetTouches[0].pageY;
        let obj = {
            top:y - this.offy + 'px',
            bottom:'auto',
            left:x - this.offx + 'px'
        }
        Object.assign(this.$refs.dragbar.style,obj)
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh; position: relative;
  }

  html {
    min-height: 100vh;
  }
  *{
    box-sizing: border-box;
  }
  .pause{
      position: fixed;
      background: #000;
      color: #fff;
      font-size: 14px;
      width: auto;
      padding: 0 5px;
      text-align: center;
      left: 0;
      bottom: 0;
      height: auto;
      white-space: nowrap;
      transition: none;
  }
  .button{
      display: inline-block;
      margin: 5px 0;
      border: 1px solid #fff;
      padding: 0 10px;
      height: 40px;
      line-height: 38px;
      transition: none;
  }
</style>



